.under_header .info{
    width: 375px;
    h3{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 22px;
        color: #222222;
        margin-bottom: 17px;
    }
    p{
        font-family: 'Open Sans';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
        color: #404756;
        width: 332px;
        margin-bottom: 32px;
    }
    &:nth-child(2){
        padding:30px 0 ;
        border-top: 1px solid black;
        border-bottom: 1px solid black;
            p{
                margin-bottom: 0;
            }
        margin-bottom: 30px;
    }
}